"use client";

import { useParams } from "next/navigation";
import { useEffect, useState } from "react";
import { supabaseClient } from "@/lib/supabaseClient";

export default function HostLobbyPage() {
  const params = useParams<{ code: string }>();
  const code = (params?.code ?? "ENTER").toString().toUpperCase();

  const [raid, setRaid] = useState<any>(null);
  const [players, setPlayers] = useState<any[]>([]);
  const [loadingStart, setLoadingStart] = useState(false);

  async function refreshState() {
    if (!code || code === "ENTER") return;

    const res = await fetch("/api/raid/state", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ code }),
    });

    const json = await res.json();
    if (json?.error) return;

    setRaid(json.raid);
    setPlayers(json.players || []);
  }

  // initial load
  useEffect(() => {
    refreshState();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [code]);

  // realtime subscribe
  useEffect(() => {
    if (!raid?.id) return;

    const channel = supabaseClient
      .channel(`host:${raid.id}`)
      .on(
        "postgres_changes",
        { event: "*", schema: "public", table: "raids", filter: `id=eq.${raid.id}` },
        (payload) => {
          const next = payload.new as any;
          setRaid((r: any) => ({ ...r, ...next }));
        }
      )
      .on(
        "postgres_changes",
        { event: "*", schema: "public", table: "players", filter: `raid_id=eq.${raid.id}` },
        () => refreshState()
      )
      .subscribe();

    return () => {
      supabaseClient.removeChannel(channel);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [raid?.id]);

  async function startRaid() {
    if (!raid || raid.status !== "lobby") return;
    setLoadingStart(true);
    try {
      const res = await fetch("/api/raid/start", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ code }),
      });
      const json = await res.json();
      if (json?.error) alert(json.error);
      await refreshState();
    } finally {
      setLoadingStart(false);
    }
  }

  const shareUrl =
    typeof window === "undefined" ? "" : `${window.location.origin}/raid/${code}`;

  return (
    <main className="pageShell">
      <div className="raidTop">
        <div>
          <div className="raidTitle">üéõÔ∏è Host Lobby ‚Äî Room {code}</div>
          {raid && (
            <div className="raidSub">
              Boss: <b>{raid.boss_name}</b> ‚Äî Status: <b>{raid.status}</b>
            </div>
          )}
          {shareUrl && (
            <div className="raidSub" style={{ marginTop: 6 }}>
              Share: <b style={{ wordBreak: "break-all" }}>{shareUrl}</b>
            </div>
          )}
        </div>

        <div className="raidRight">
          <button className="btn" onClick={refreshState}>
            Refresh
          </button>
        </div>
      </div>

      <div className="grid2">
        <div className="card">
          <h2 style={{ marginTop: 0 }}>Controls</h2>

          <div className="pill">
            <div style={{ fontWeight: 900, marginBottom: 6 }}>Room Code</div>
            <div style={{ fontSize: 22 }}>
              <b>{code}</b>
            </div>
          </div>

          <div style={{ marginTop: 14, display: "flex", gap: 10, alignItems: "center", flexWrap: "wrap" }}>
            <button
              className="btnPrimary"
              onClick={startRaid}
              disabled={!raid || raid?.status !== "lobby" || players.length < 1 || loadingStart}
              title="Start when ready"
            >
              {loadingStart ? "Starting..." : "Start Raid"}
            </button>

            <span style={{ opacity: 0.75, fontSize: 12 }}>
              Players joined: <b>{players.length}</b>
            </span>
          </div>

          <div style={{ opacity: 0.7, fontSize: 12, marginTop: 10 }}>
            Tip: keep this tab on the projector. Players join from the share link. When you press Start, everyone switches to battle automatically.
          </div>
        </div>

        <div className="card">
          <h3 style={{ marginTop: 0 }}>Players</h3>
          <div className="list">
            {players.map((p) => (
              <div key={p.id} className="row">
                <div className="name">{p.display_name}</div>
                <div className="muted">DMG {p.total_damage}</div>
              </div>
            ))}
            {players.length === 0 && <div className="muted">No one joined yet.</div>}
          </div>
        </div>
      </div>
    </main>
  );
}
